.node-editor-container {
  overflow: hidden;
  border: 1px solid red;
}

.node-editor-background {
  position: absolute;
  top: -10000px;
  right: -10000px;
  left: -10000px;
  bottom: -10000px;
  opacity: 0.1;
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAICAYAAADED76LAAAAJElEQVQoU2NkYGD4z4AKGJG5IA4dFKA5AdVKFAdBVaK4iXIFAEiuCAWq9MdHAAAAAElFTkSuQmCC");
}

.node-editor-container .connection {
  overflow: visible;
  position: absolute;
  z-index: -1;
}

.node-editor-container .connection .main-path {
  fill: none;
  stroke-width: 5px;
  stroke: blue;
}

.node-wrapper .node {
  position: relative;
  background-color: white;
  cursor: grab;
  border: 1px solid transparent;
}

.node-wrapper .node.selected {
  border: 1px solid blue;
}

.node-wrapper .node .node-content {
  padding: 2px;
}

.node-wrapper .node .node-input,
.node-wrapper .node .node-output {
  display: flex;
  justify-content: space-between;
}

.node-wrapper .node .node-socket {
  height: 10px;
  width: 10px;
  background-color: gray;

  cursor: pointer;
}